import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit, faTimes, faInbox } from "@fortawesome/free-solid-svg-icons";

import BasicTabs from "../../components/BasicTabs";
import ComicMap from "../../components/Comic/ComicMap";
import VideoMap from "../../components/Video/VideoMap";
import { Product } from "../../type/favorite";

// { setSelectItem, removeFavoriteItem } from "../../store/favoritesSlice";
import {
  favorite,
  myFavorite,
  setSelectItem,
} from "../../store/favoritesSlice";
import { RootState, AppDispatch, store } from "../../store";
import { useSelector, useDispatch } from "react-redux";
import { fetchComicInfo } from "../../store/comicSlice";

import { FavoriteItem } from "../../utils/pouchdb";
import { Favorite, MyFavoriteData, PostParam } from "../../type/favorite";
import { showCustomToast } from "../../components/CustomToast";
import { getFontClass, isLogin } from "../../utils/helper";

interface Comic {
  _id: number,
  ci_uuid: string,
  ci_name: string,
  pic_big: string
}

interface AllProps {
  filter: string;
}

const All: React.FC<AllProps> = ({filter}) => {
  const { t } = useTranslation();
  const [isEditing, setIsEditing] = useState<boolean>(false);
  const [selectedItems, setSelectedItems] = useState<string[]>([]);
  const [allData, setAllData] = useState<Product[]>([]);
  const [currentPageNum, setCurrentPageNum] = useState<number>(1);
  const [comic, setComic] = useState<Comic[]>([]);
  const [video, setVideo] = useState<Product[]>([]);
  const language = useSelector((state: RootState) => state.init.language);

  //const favorite = useSelector((state: RootState) => state.favorites);
  const myFavoriteData = useSelector(
    (state: RootState) => state.favorites.myFavoriteData
  );

  const dispatch: AppDispatch = useDispatch();
  
  const handleGetMoreData = () => {
    dispatch(
      myFavorite({
        num: (currentPageNum + 1).toString()
      })
    );
    setCurrentPageNum(currentPageNum + 1);
  }
  const updateAllData = () => {
    if (
      myFavoriteData 
      && currentPageNum !== 1
    ) {
      setAllData((prevAllData) => [...prevAllData, ...myFavoriteData.products]);
    }
  }
  const handleInitAllData = () => {
    if (
      myFavoriteData 
      && currentPageNum === 1
    ) {
      setAllData([]);
      setAllData(myFavoriteData.products);
    }
  };

  useEffect(() => {
    dispatch(myFavorite({}));
  }, [dispatch]);

  useEffect(() => {
    handleInitAllData();
    updateAllData();
  }, [myFavoriteData])

  useEffect(() => {
    if (allData && allData.length !== 0) {
      // 篩選出有 pic_big 的資料

      const tmpComics = allData.filter(
        (item) => 
            item.pic_big 
            && item.name.toLowerCase().includes(filter.toLowerCase())
      );
      let comics:Comic[] = [];
      tmpComics.forEach((item, index) => {
        let obj = {
          _id: index,
          ci_uuid: item.uuid,
          ci_name: item.name,
          pic_big: item.pic_big ?? ''
        };
        comics.push(obj);

      })
      setComic(comics);

      // 篩選出沒有 pic_big 的資料
      const videos = allData.filter(
        (item) => 
          !item.pic_big
          && item.name.toLowerCase().includes(filter.toLowerCase())
      );
      setVideo(videos);
      console.log('videos:', videos);
    }
  }, [allData, filter]); 

  const toggleEditMode = () => {
    setIsEditing(!isEditing);
    setSelectedItems([]);
  };

  const toggleSelectItem = (id: string) => {
    setSelectedItems((prevSelected) =>
      prevSelected.includes(id)
        ? prevSelected.filter((itemId) => itemId !== id)
        : [...prevSelected, id]
    );
    dispatch(setSelectItem(selectedItems));
  };

  const deleteSelectedItems = () => {
    // console.log("刪除選中的項目:", selectedItems);
    // console.log(selectedItems);
    const config: PostParam = {
      data: JSON.stringify(selectedItems),
    };
    dispatch(favorite({ method: "delVideo", post_param: config }))
      .then((result) => {
        const payload = result.payload as Favorite;
        if (payload.code === -1) {
          dispatch(myFavorite({}));
          showCustomToast("刪除成功", "error");
        } else {
          console.log("delFolder exception: ", payload);
        }
      })
      .catch((error) => {
        showCustomToast(error, "error");
      });

    setSelectedItems([]);
    setIsEditing(false);
  };

  const tabContent = [
    {
      key: "影片",
      label: t("影片"),
      content:
        myFavoriteData?.products.length === 0 ? (
          <div className="flex flex-wrap justify-center px-2 pb-2 min-h-screen w-full relative">
            <div
              className="flex flex-col justify-center p-4 w-3/5 absolute"
              style={{ top: "20%" }}
            >
              <FontAwesomeIcon
                icon={faInbox}
                size="6x"
                className="text-gray-200 mb-2"
              />
              <p className={`${getFontClass(language)} text-gray-200 text-center font-bold`}>
                {t("快加入喜歡的內容")}
              </p>
            </div>
          </div>
        ) : (
          <VideoMap
            data={video}
            isLinkEnabled={!isEditing}
            toggleSelectItem={toggleSelectItem}
            selectItems={selectedItems}
            onReachBottom={handleGetMoreData}
            lastPage={currentPageNum === myFavoriteData?.pageTotals}
          />
        ),
    },
    {
      key: "漫畫",
      label: t("漫畫"),
      content:
        myFavoriteData?.products.length === 0 ? (
          <div className="flex flex-wrap justify-center px-2 pb-2 min-h-screen min-w-full relative">
            <div
              className="flex flex-col justify-center p-4 w-3/5 absolute"
              style={{ top: "20%" }}
            >
              <FontAwesomeIcon
                icon={faInbox}
                size="6x"
                className="text-gray-200 mb-2"
              />
              <p className={`${getFontClass(language)} text-gray-200 text-center font-bold`}>
                {t("快加入喜歡的內容")}
              </p>
            </div>
          </div>
        ) : (
          <ComicMap
            data={comic}
            isLinkEnabled={!isEditing}
            toggleSelectItem={toggleSelectItem}
            onReachBottom={handleGetMoreData}
            lastPage={currentPageNum === myFavoriteData?.pageTotals}
          />
        ),
    },
  ];

  return (
    <div className="pb-10">
      <div className="flex justify-end p-4">
        {isLogin(store.getState()) && (
          <button onClick={toggleEditMode} className="ml-auto">
            <FontAwesomeIcon icon={isEditing ? faTimes : faEdit} />
            <span>{isEditing ? t("取消") : t("編輯")}</span>
          </button>
        )}
      </div>

      <BasicTabs tabs={tabContent} />
      {/* VideoMap component */}

      {/* Delete button at the bottom */}
      {isEditing && (
        <div className="fixed bottom-0 right-0 w-full bg-background p-4 text-white flex justify-between items-center z-10">
          <span>
            {t("刪除")} ({selectedItems.length})
          </span>
          <button
            onClick={deleteSelectedItems}
            className="bg-red-500 px-4 py-2 rounded-full text-white"
          >
            {t("刪除")}
          </button>
        </div>
      )}
    </div>
  );
};

export default All;
