import React, { useEffect, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEdit,
  faCirclePlus,
  faClose,
  faCheck,
  faInbox,
  faFolder
} from "@fortawesome/free-solid-svg-icons";
import { useTranslation } from "react-i18next";
import BasicModal from "../../components/BasicModal";
import { useNavigate } from "react-router-dom";

import { RootState, AppDispatch, store } from "../../store";
import { useSelector, useDispatch } from "react-redux";
import { FavoriteItem } from "../../utils/pouchdb";
import { favorite } from "../../store/favoritesSlice";
import { Favorite, PostParam } from "../../type/favorite";
import { showCustomToast } from "../../components/CustomToast";
import { Option } from "../../type/common";
import Modal from "../../components/Modal";
import Button from "../../components/Button";
import { getFontClass, isLogin } from "../../utils/helper";
//import { addFavoriteItem, removeFavoriteItem } from "../../store/favoritesSlice";

interface FolderData {
  id: number;
  title: string;
  description: string;
  imageUrl: string;
}

const File: React.FC = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const dispatch:AppDispatch = useDispatch();

  const folderList = useSelector((state: RootState) => state.favorites.folderList);
  
  // State management
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [folderName, setFolderName] = useState("");
  const [folderDescription, setFolderDescription] = useState("");
  const [data, setData] = useState<FavoriteItem[]>([]);

  const [isEditing, setIsEditing] = useState(false);
  const [selectedFolders, setSelectedFolders] = useState<Option[]>([]);
  const language = useSelector((state: RootState) => state.init.language);

  useEffect(() => {
    dispatch(favorite({method: 'getAllFolder'}));
  }, [dispatch]);
  useEffect(() => {
    console.log(folderList);
  }, [folderList]);

  useEffect(() => {
    console.log(selectedFolders);
  }, [selectedFolders]);

  // Modal handling
  const handleOpenModal = () => setIsModalOpen(true);
  const handleCloseModal = () => setIsModalOpen(false);

  const handleAddFolder = () => {
    if (folderName.trim()) {
      const config: PostParam = {
        folder_name: folderName,
        folder_memo: folderDescription

      };
      dispatch(favorite({method: 'addFolder', post_param: config}))
      .then((result) => {
        dispatch(favorite({method: 'getAllFolder'}))
        .then((result) => showCustomToast(t("新增成功!"), 'error'));
      })
      .catch((error) => {showCustomToast(error, 'error')})
      setFolderName("");
      setFolderDescription("");
      setIsModalOpen(false);
    }
  };
  const handleEditMode = () => {
    setIsEditing((prev) => !prev);
    setSelectedFolders([]);
  };

  const toggleSelectFolder = (option: Option) => {
    setSelectedFolders((prevSelected) =>
      prevSelected.some(item => item.id === option.id)
        ? prevSelected.filter((item) => item.id !== option.id)
        : [...prevSelected, option]
    );
  };

  const handleDeleteFolders = () => {
    handleDeleteModalClose();
    if (selectedFolders.length === 0) {
      return;
    }
    //const id = JSON.stringify(selectedFolders.map(selectedFolder => selectedFolder.id));
    //console.log(id);
    const config:PostParam = {
      id: selectedFolders[0].id
    }
    dispatch(favorite({method: 'delFolder', post_param: config}))
    .then((result) => {
      const payload = result.payload as Favorite;
      if (payload.code === -1) {
        dispatch(favorite({method: 'getAllFolder'}))
        .then((result) => showCustomToast(t('刪除成功!'), 'error'))
      } else {
        console.log('delFolder exception', payload);
      }
    })
    .catch((error) => {showCustomToast(error ,'error')})

    setSelectedFolders([]);
    setIsEditing(false);
  };

  const handleFolderClick = (folderName: string, folderId: string) => {
    navigate(`/app/folder/${folderId}/videos?name=${folderName}&id=${folderId}`);
  };

  const handleDeleteModalClose = () => {setIsDeleteModalOpen(false)};
  const handleDeleteModalOpen = () => {setIsDeleteModalOpen(true)};

  const handleDeleteModalButtons = [
    <Button className={`${getFontClass(language)} button-full active mt-1 w-1/3`} type="button" onClick={handleDeleteFolders}>
      {t("確認")}
    </Button>,
    <Button className={`${getFontClass(language)} button-full active mt-1 w-1/3`} type="button" onClick={handleDeleteModalClose}>
      {t("取消")}
    </Button>
  ];

  return (
    <div>
      {/* Top Action Buttons */}
      {isLogin(store.getState()) && (
        <div className="flex justify-end p-4">
          <button className="ml-auto" onClick={handleEditMode}>
            {isEditing ? (
              <>
                <FontAwesomeIcon icon={faClose} />
                <span> {t("取消")}</span>
              </>
            ) : (
              <>
                <FontAwesomeIcon icon={faEdit} />
                <span> {t("編輯")}</span>
              </>
            )}
          </button>
          {!isEditing && (
            <button onClick={handleOpenModal} className="ml-8">
              <FontAwesomeIcon icon={faCirclePlus} />
              <span>{t("新增")}</span>
            </button>
          )}
        </div>
      )}

      {/* Modal for adding folder */}
      <BasicModal
        options={null} // 根據需求傳入 options 或 null
        open={isModalOpen}
        currentState={null} // 根據需求傳入 currentState 或 null
        handleClose={handleCloseModal}
        setSelectedOption={() => {}} // 傳入一個空函數或實際函數
        title={t("新增資料夾")}
        content={
          <>
            <label className="block mb-2">{t("資料夾名稱")}</label>
            <input
              type="text"
              value={folderName}
              onChange={(e) => setFolderName(e.target.value)}
              className="border p-2 w-full mb-4 text-black rounded"
              placeholder={t("輸入資料夾名稱")}
            />
            <label className="block mb-2">{t("註解")}</label>
            <textarea
              value={folderDescription}
              onChange={(e) => setFolderDescription(e.target.value)}
              className="border p-2 w-full mb-4 text-black rounded"
              placeholder={t("輸入註解")}
            ></textarea>
          </>
        }
        onConfirm={handleAddFolder}
        onCancel={handleCloseModal}
        showButtons={true}
      />

      {/* Folder Display with Edit Mode Checkboxes */}
      <div className="flex flex-wrap px-0  min-h-screen min-w-screen relative">
        {!isLogin(store.getState()) || (Array.isArray(folderList?.data) && folderList?.data.length === 0) ? (
          <div className="flex justify-center p-4 w-full h-full">
            <div className="flex flex-col justify-center p-4 w-3/4 h-full" style={{marginTop: "40%"}}>
              <FontAwesomeIcon icon={faInbox} size="6x" className="text-gray-200 mb-2" />
              <p className={`${getFontClass(language)}  text-gray-200 text-center font-bold`}>
                {t("快加入喜歡的內容")}
              </p>
            </div>
          </div>
        ): (
          folderList?.data?.map((item) => (
            <div
              key={item.folder_id}
              className="relative flex flex-col items-center w-1/3 p-4"
              onClick={() => {
                if (isEditing) {
                  toggleSelectFolder({id: item.folder_id, name: item.folder_name});
                } else {
                  handleFolderClick(item.folder_name, item.folder_id);
                }
              }}
            >
              <FontAwesomeIcon icon={faFolder} size="4x" className="text-slate-600 mb-2" />
              <span className="text-sm line-clamp-2 text-center">{t(item.folder_name)}</span>
  
              {isEditing && (
                <div className="absolute right-3 top-2 flex items-center justify-center">
                  {selectedFolders.some((selectedFolder) => selectedFolder.id === item.folder_id) ? (
                    <div className="w-6 h-6 bg-red-500 rounded-full flex items-center justify-center">
                      <FontAwesomeIcon icon={faCheck} className="text-white" />
                    </div>
                  ) : (
                    <div className="w-6 h-6 border-2 border-gray-400 rounded-full"></div>
                  )}
                </div>
              )}
            </div>
          ))
        )}
      </div>

      {/* Delete Button */}
      {isEditing && (
        <div className="fixed bottom-0 left-0 w-full bg-background p-4 text-white flex justify-between items-center z-10">
          <span>
            {t("刪除")} ({selectedFolders.length})
          </span>
          <button
            onClick={handleDeleteModalOpen}
            className="bg-red-500 px-4 py-2 rounded-full text-white"
          >
            {t("刪除")}
          </button>
        </div>
      )}
      <Modal
        open={isDeleteModalOpen}
        onClose={handleDeleteModalClose}
        content={[selectedFolders.length > 0 ? `${t("確定要刪除")}${selectedFolders[0].name}` : t("沒有選擇資料夾")]}
        buttons={handleDeleteModalButtons}
      />
    </div>
    
  );
};

export default File;
